import React, { Component , Fragment  } from 'react'
import { withRouter } from 'react-router-dom'
import { _setChildDOM } from '@/tool/Processing.js'
import signal from '@/assets/imgs/signal.gif'
import './index.scss'
const getTime = (time) => Math.round(time/60)
class Cardchildred extends Component {
    _changePage = (children)=>{
        const { lineId,siteOrder } = this.props
        const { upDown } = children
        this.props.history.push({pathname:'/details',state:{lineId,siteOrder,upDown}})
    }
    _getChildrenDom = ( children )=>{
        let  childrenDOM
        if( children.pullIn===2 ){
            childrenDOM=(
                <div className="three_two">
                    <div className="two_top">
                        <img src={signal} alt="" />
                    </div>
                    {children.drawInTime>60
                    ?<div className="two_cen">{getTime(children.drawInTime)}  <span>分</span></div>
                    :<div className="two_cen">{children.drawInTime}  <span>秒</span></div>
                    }
                    <div className="two_bottom" onClick={()=>this._changePage(children)}>查看更多</div>
                </div>)
        }
        if( children.pullIn===4 ){childrenDOM=(<div className="three_twotext">即将到站</div>)}
        if(  children.pullIn===3 ){childrenDOM=(<div className="three_twotext">已到站</div>)}
        return childrenDOM
    }
    _getDOM = ( index,list  ) =>{
        let updateDOM
        let newList
        if( index===1 ) {
            updateDOM=(
                <div className="right_one">
                    <div className="one_text">发车中...</div>
					<div onClick={()=>this._changePage()}>查看更多</div>
                </div>)
        }
         if( index===2 ){
              newList=_setChildDOM(list)||[]
             if(newList.length){
                updateDOM=this._getChildrenDom(newList[0])
             }else{
                updateDOM=(
                <div className="right_one">
                    <div className="one_text">发车中...</div>
                    <div onClick={()=>this._changePage()}>查看更多</div>
                </div>)
             }
            
        }
        if(index===3){updateDOM=(<div className="right_three">已收班</div>)}
        return updateDOM
    }
    render() {
        const { status ,list } =this.props
        return (
            <Fragment>
               { this._getDOM( status,list ) } 
            </Fragment>
        )
    }
}

export default withRouter(Cardchildred)
