import React, { Component } from 'react'
import {connect} from 'react-redux'//引入redux容器组件
import { RECOMMENDLINE } from '@/redux/recommend/variable.js'
import { _unique ,_setCacheList,_delCache} from '@/tool/Processing.js'
import home from '@/assets/imgs/home.png'
import company from '@/assets/imgs/company.png'
import fav from '@/assets/imgs/fav.png'
import del from '@/assets/imgs/del.png'
import './index.scss'
const selectImg=[
    { id: 1, img: home, title: '家' },
    { id: 2, img: company, title: '公司' },
    { id: 3, img: fav, title: '其他' },
    { id: 0, img: del, title: '删除' },
  ]
class Bulletframe extends Component {
    state={
        
    }
    _selectState = (flagId) =>{
        const { myLineData , soleId , switchID} =this.props
        let listObj
        let Collection
        let ction
        if( flagId ){
            if(switchID===2){
                Collection=JSON.parse(localStorage.getItem('historyList'))||[]
                for(let i=0;i<myLineData.length;i++){
                    if(myLineData[i].soleId===soleId){
                        listObj=myLineData[i]
                        myLineData[i].flag=flagId
                        break;
                    }
                }
                Collection.push(listObj)
                ction=_unique(Collection)
                ction = ction.map(t =>  t.soleId === listObj.soleId ? listObj : t);
                localStorage.setItem('historyList',JSON.stringify(_setCacheList(ction)))
            }else{
                Collection=JSON.parse(localStorage.getItem('collection'))||[]
                for(let i=0;i<myLineData.length;i++){
                    if(myLineData[i].soleId===soleId){
                        listObj=myLineData[i]
                        myLineData[i].flag=flagId
                        break;
                    }
                }
                Collection.push(listObj)
                ction=_unique(Collection)
                ction = ction.map(t =>  t.soleId === listObj.soleId ? listObj : t);
                localStorage.setItem('collection',JSON.stringify(_setCacheList(ction)))
            }
        }else{
            if(switchID===2){
                Collection=JSON.parse(localStorage.getItem('historyList'))||[]
                ction=_delCache(Collection,soleId)
                localStorage.setItem('historyList',JSON.stringify(ction))
            }else{
                Collection=JSON.parse(localStorage.getItem('collection'))||[]
                ction=_delCache(Collection,soleId)
                localStorage.setItem('collection',JSON.stringify(ction))
            }
            console.log(switchID);
            myLineData.forEach(element => {
                if(element.soleId===soleId){
                    element.flag=0
                }
            });
        }
        this.props._changerecommend(myLineData)//将数据传到公共状态中
        this.props.changeFeame(false)
    }
    render() {
        return (
            <div className="frame">
                <div className="frame_top" onClick={()=>{this.props.changeFeame(false)}}></div>
                <div className="frame_fl slidup">
                    <div className="fl_select">
                        {
                            selectImg.map(item=>{
                                return (
                                    <div className="select_box" key={item.id} onClick={()=>this._selectState(item.id)}>
                                        <img src={item.img} alt="" />
                                        <div>{item.title}</div>
                                    </div>
                                )
                            }) 
                        }
                    </div>
                    <div className="fl_cancel" onClick={()=>{this.props.changeFeame(false)}}>取消</div>
                </div>
            </div>
        )
    }
}
const mapStateToProps=(state)=>({
    myLineData:state.recommendData,
    switchID:state.switchState
})
const mapDisPatchToProps=(dispatch)=>({
    _changerecommend:( busReal ) =>{
        dispatch({type:RECOMMENDLINE,data:busReal })
    }
})


export default connect(mapStateToProps,mapDisPatchToProps)(Bulletframe)

